

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
 
  .bg-container {
    background-image: url('./background/mia1.jpg'); 
    background-size: cover;
    background-position: center;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    overflow: hidden;
    animation: fadeInBackground 5s ease-out;
  }
  
  
  .form {
    background-image: url('./background/mia 2.jpg'); 
    border-radius: 20px;
    padding: 40px 60px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 400px;
    text-align: center;
    animation: formSlideUp 1.5s ease-out;
  }
  
  
  .input {
    width: 100%;
    padding: 14px 20px;
    margin: 10px 0;
    border: 2px solid #ccc;
    border-radius: 8px;
    font-size: 16px;
    background-color: transparent;
    transition: all 0.3s ease;
  }
  
  .input:focus {
    border-color: #4CAF50;
    outline: none;
    box-shadow: 0 0 8px rgba(76, 175, 80, 0.6);
  }
  
  
  .button {
    background-color: #4CAF50; /* Green Button */
    color: white;
    border: none;
    padding: 15px 20px;
    margin: 10px 0;
    border-radius: 8px;
    font-size: 18px;
    width: 100%;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .button:hover {
    background-color: #45a049;
    transform: translateY(-3px);
  }
  
  
  .errorMsg {
    font-size: 14px;
    color: #ff4c4c;
    margin-bottom: 15px;
  }
  
 
  h1.login {
    color: #fff;
    font-size: 2.2rem;
    font-weight: bold;
    letter-spacing: 3px;
    margin-bottom: 20px;
    animation: headerBounceIn 1s ease-out;
  }
  
  
  h3.sign {
    font-size: 1.5rem;
    color: rgb(240, 222, 222);
    margin-bottom: 20px;
    animation: fadeInText 1s ease-in;
  }
  
 
  @keyframes fadeInBackground {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes formSlideUp {
    0% {
      opacity: 0;
      transform: translateY(30px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes headerBounceIn {
    0% {
      transform: translateY(-50px);
      opacity: 0;
    }
    60% {
      transform: translateY(15px);
      opacity: 1;
    }
    100% {
      transform: translateY(0);
    }
  }
  
  @keyframes fadeInText {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  /* Responsive Design */
  @media (max-width: 600px) {
    .form {
      padding: 25px 35px;
    }
    h1.login {
      font-size: 1.8rem;
    }
    .button {
      font-size: 16px;
    }
  }
  